import React from "react";
import { Link, useNavigate } from "react-router-dom";
import "./PrivacyPolicy.css";
import backButton from "./backButton.png";

function PrivacyPolicy() {
  const navigate = useNavigate();

  return (
    <>
      <div className="privacyPolicy">
        <header>
          <h1>Privacy Policy</h1>
          <div>
            <img src={backButton} onClick={() => navigate(-1)} />
          </div>
        </header>
        <content>
          <p>Последнее обновление: 11.10.2023 г.</p>
          <p>
            Эта политика конфиденциальности объясняет, какие данные собирает и
            использует приложение Alva, а также их защиту. Прочитайте эту
            политику внимательно, чтобы понять, какие данные мы собираем и как
            вы можете управлять ими.
          </p>
          <b>Собираемые данные</b>
          <p>Мы можем собирать следующие типы данных:</p>
          <p>
            Персональные данные: Номер телефона, который вы предоставляете нам
            добровольно.
          </p>
          <p>
            Данные использования: Информация о том, как вы используете
            приложение, включая дату и время взаимодействия с ним.
          </p>
          <b>Использование данных</b>
          <p>Мы используем собранные данные для следующих целей:</p>
          <p>
            Предоставление сервиса: Для предоставления функций и сервисов,
            доступных в приложении.
          </p>
          <p>
            Анализ и улучшение: Для анализа и улучшения работы приложения, а
            также для разработки новых функций.
          </p>
          <b>Разглашение данных третьим сторонам</b>
          <p>Мы не разглашаем ваши персональные данные третьим сторонам.</p>
          <b>Управление данными</b>
          <p>
            Вы можете запросить удаление ваших персональных данных, связавшись с
            нами по адресу supportcharge@alvacharge.kz.
          </p>
          <b>Безопасность данных</b>
          <p>
            Мы принимаем меры для защиты ваших данных от несанкционированного
            доступа, утраты или утечки.
          </p>
          <b>Изменения в политике конфиденциальности</b>
          <p>
            Мы можем время от времени обновлять эту политику конфиденциальности.
            Важные изменения будут опубликованы здесь.
          </p>
          <b>Связаться с нами</b>
          <p>
            Если у вас есть вопросы или предложения относительно нашей политики
            конфиденциальности, пожалуйста, свяжитесь с нами по адресу
            supportcharge@alvacharge.kz.
          </p>
          <div className="button" onClick={() => navigate(-1)}>
            Назад
          </div>
        </content>
      </div>
    </>
  );
}

export default PrivacyPolicy;
